import { Injectable, InjectionToken } from "@angular/core";

@Injectable({
  providedIn: "root"
})
export class Config {
  get(key: keyof UIXconfig, fallback?: any): any {
    const c = getConfig();
    if (c) {
      return c.get(key, fallback);
    }
    return null;
  }

  getBoolean(key: keyof UIXconfig, fallback?: boolean): boolean {
    const c = getConfig();
    if (c) {
      return c.getBoolean(key, fallback);
    }
    return false;
  }

  getNumber(key: keyof UIXconfig, fallback?: number): number {
    const c = getConfig();
    if (c) {
      return c.getNumber(key, fallback);
    }
    return 0;
  }

  set(key: keyof UIXconfig, value?: any) {
    const c = getConfig();
    if (c) {
      c.set(key, value);
    }
  }
}

export interface UIXconfig {
  id: string;
  name: string;
  version: string;
}

export const ConfigToken = new InjectionToken<any>("USERCONFIG");

function getConfig(): any {
  if (typeof (window as any) !== "undefined") {
    const UIX = (window as any).UIX;

    if (UIX && UIX.config) {
      return UIX.config;
    }
  }
  return null;
}
