import { DOCUMENT } from "@angular/common";
import {
  animate,
  style,
  group,
  query,
  transition,
  trigger
} from "@angular/animations";

import { Platform } from "../providers/platform";

const plt = new Platform(DOCUMENT);

const DURATION = 500;
const EASING = "cubic-bezier(0.36,0.66,0.04,1)";
const OPACITY = "opacity";
const TRANSFORM = "transform";
const TRANSLATEX = "translateX";
const CENTER = "0%";
const OFF_OPACITY = 0.8;

const ios = {
  duration: "500ms",
  backwardDuration: "500ms",
  timingFunction: EASING
};
const md = {
  duration: "250ms",
  backwardDuration: "400ms",
  timingFunction: "ease-out"
};

const vars = plt.is("ios") ? ios : md;

const MaterialTranstion = {
  forward: [
    transition("* <=> *", [
      style({ transform: "translateX(0)" }),
      query(":enter", [style({ transform: "translateX(100%)" })]),
      query(":leave", [style({ transform: "translateX(0)" })]),
      group([
        query(":enter", [
          animate(
            vars.duration + " " + vars.timingFunction,
            style({ transform: "translateX(0)" })
          )
        ]),
        query(":leave", [
          animate(
            vars.duration + " " + vars.timingFunction,
            style({ transform: "translateX(-20%)" })
          )
        ])
      ])
    ])
  ],
  back: [
    transition("* <=> *", [
      style({ transform: "translateX(0)" }),
      query(":enter", [style({ transform: "translateX(100%)" })]),
      query(":leave", [style({ transform: "translateX(0)" })]),
      group([
        query(":enter", [
          animate(
            vars.duration + " " + vars.timingFunction,
            style({ transform: "translateX(0)" })
          )
        ]),
        query(":leave", [
          animate(
            vars.duration + " " + vars.timingFunction,
            style({ transform: "translateX(-20%)" })
          )
        ])
      ])
    ])
  ]
};

const IosTranstion = {
  forward: [
    transition("HomeComponent <=> AboutComponent", [
      query(":enter", [style({ transform: "translateX(100%)" })]),
      query(":leave", [style({ transform: "translateX(0)" })]),
      group([
        query(":leave", [
          animate("400ms ease-out", style({ transform: "translateX(0)" }))
        ]),
        query(":enter", [
          animate("400ms ease-out", style({ transform: "translateX(-20%)" }))
        ])
      ])
    ])
  ],
  back: [
    transition("* <=> *", [
      style({ transform: "translateX(0)" }),
      query(":enter", [style({ transform: "translateX(100%)" })]),
      query(":leave", [style({ transform: "translateX(0)" })]),
      group([
        query(":enter", [
          animate(
            vars.duration + " " + vars.timingFunction,
            style({ transform: "translateX(0)" })
          )
        ]),
        query(":leave", [
          animate(
            vars.duration + " " + vars.timingFunction,
            style({ transform: "translateX(-20%)" })
          )
        ])
      ])
    ])
  ]
};

const direction = "forward";
const transitions = plt.is("ios") ? IosTranstion : MaterialTranstion;

export const RouterTransition = trigger(
  "routerTransition",
  transitions[direction]
);
